.t-wrapper{
    padding: 0 3rem 0 3rem;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 7rem;
    flex-direction: column;
    gap: 2rem;
    position: relative;

}

.t-heading{
    align-self: flex-start;
}
.t-heading >*{
    font-size: 2rem;
    font-weight: bold;
    /* padding-left: 13px; */
}

.t-heading span:nth-of-type(2){
    color: var(--orange);
    padding: 13px;
}

.t-blur1{
    left: 38rem;
    top: 16rem;
}

.t-blur2{
    top: 9rem;
    left: 0;
}
.testimonial{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    width: 30rem;
    height: 12rem;
    /* style same as work.css */
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--blueCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}

.testimonial >img {
    width: 6rem;
    height: 6rem;
    border-radius: 100%;
}
.testimonial>span{
    color: var(--gray);
    
}
/* to show slide */
.t-wrapper .swiper{
    overflow: visible!important;
}
/* slider */

.t-wrapper .swiper{

    width: 100%;
    height: 70%;
}
.t-wrapper .swiper-slide{
    
    display: flex;
    align-items: center;
    justify-content: center;
}

/* responsive */
@media screen and (max-width: 480px) {
    .t-wrapper{
        padding: 0;
    }

    .testimonial>img{
        width: 70px;
        height: 70px;
    }
}